
define('token',[],function() {
	var token = localStorage.getItem("token");
	console.log(token);
	if (token == null) {
		var url = window.location.href;
		var param = url.split("?")[1];
		if(param){
			token = param.split("=")[1];
			token = token.split("#")[0];
		}
		localStorage.setItem("token", token);
	}
});

