
define('mvc/collections/BulletinsCollection',[ "jquery", "backbone", "mvc/models/BulletinModel","moment" ], function($,
		Backbone, BulletinModel,moment) {
	var Collection = Backbone.Collection
			.extend({
				initialize : function(models, options) {
					this.pageInfo = {
						pageSize : 10
					}
				},
				model : BulletinModel,
				sync : function(method, model, options) {
					var self = this;
					// 如果为全部则不设置分类
					var bulletinTypeId = "";
					if (options.type != "all") {
						bulletinTypeId= options.type;
					}
					options.pageNo = options.pageNo||1;
					var xhr = Backbone.ajax({
						url : window.local?"bulletin.json":"/ecp/bulletin/validBulletin.json",
						type : 'POST',
						dataType:"json",
						data : {
							"newSearchId" : "1",
							"oldSearchId" : "",
							"pageInfo.curPageNo" : options.pageNo,
							"pageInfo.pageSize" : self.pageInfo.pageSize,
							"bulletinTypeId" : bulletinTypeId
						},
						success : function(data) {
							data = self.formatDate(data);
							if( options.pageNo>1){
								self.push(data.records);
								self.trigger("push",data);
							}else{
								self.reset(data.records);
								self.trigger("load",data,options);
							}
							self.trigger("page",data,self.pageInfo,options);
						}
					});
					return xhr;
				},
				formatDate:function(data){
					var records = data.records;
					moment.lang("zh-cn");
					for(var i=0;i<records.length;i++){
						records[i].teffectivedate = moment(records[i].teffectivedate).format("YYYY-MM-DD hh:mm:ss");
						records[i].fromNow = moment(records[i].teffectivedate).fromNow();
					};
					data.records = records;
					return data;
				},
				detail : function(obj) {
					if (obj == null)
						return void 0;
					this.trigger("detail", obj);
					return this._byId[obj.id] || this._byId[obj.cid]
							|| this._byId[obj];
				}
			});
	return Collection;
});