
define('mvc/views/BulletinView',[ "jquery", "backbone", "mvc/models/BulletinModel" ], function($,
		Backbone, CategoryModel) {
	var CategoryView = Backbone.View
			.extend({
				initialize : function() {
					this.collection.on("push", this.push, this);
					this.collection.on("load", this.load, this);
					this.collection.on("page", this.page, this);
				},
				load : function(data, options) {
					this.template = _.template(
							$("script#bulletinItems").html(), {
								"collection" : this.collection.toJSON()
							});
					var $list = this.$el.find("#J-bulletinList");
					if(options.category){
						this.$el.find("#J_bulletinTypeName").html(
								options.category.attributes.name);
					}
					$list.html(this.template);
					$list.listview("refresh");
					$.mobile.loading("hide");
//					var p=$('.bulletinItem p');
//					var divh=$('#fos').height();
//					while ($(p).outerHeight()>divh) {
//					    $(p).text(function (index, text) {
//					        return text.replace(/\W*\s(\S)*$/, '...');
//					    });
//					}
					return this;
				},
				push : function(data) {
					if (data.records.length > 0) {
						var $list = this.$el.find("#J-bulletinList");
						// 给公告列表设置公告分类
						// 如果页码大于1则需要添加到页尾
						this.template = _.template($("script#bulletinItems")
								.html(), {
							"collection" : data.records
						});
						$list.append(this.template);
						$list.listview("refresh");
					}
					$.mobile.loading("hide");
					return this;
				},
				page : function(data, pageInfo, options) {
					// 处理更多按钮
					$("#J_loadMore").hide();
					if (options.type == "") {
						options.type = "all";
					}
					var url = "#list?" + options.type + "/"
							+ (parseInt(options.pageNo) + 1);
					if (data.total > pageInfo.pageSize
							&& data.records.length > 0) {
						$("#J_loadMore").find("a").attr("href", url);
						if (data.records.length == pageInfo.pageSize) {
							$("#J_loadMore").show();
						}
					} else {
//						$("#J_noMore").popup("open");
//						setTimeout(function() {
//							$("#J_noMore").popup("close");
//						}, 2000);
					}
				}
			});
	return CategoryView;
});