
// Category View
// =============

// Includes file dependencies
define('mvc/views/DetailView',[ "jquery", "backbone", "mvc/models/BulletinModel" ], function($, Backbone,
		BulletinModel) {
	var CategoryView = Backbone.View.extend({
		initialize : function() {
			this.collection.on("detail", this.detail, this);
		},
		detail:function(id){
			var detail = this.collection.get(id);
			this.template = _.template($("script#bulletinDetail")
					.html(), {
				"detail" : detail.toJSON()
			});
			this.$el.find("#J_bulletinContent").html(this.template);
		}
	});
	return CategoryView;
});