
define('mvc/routers/mobileRouter',[ "token", "jquery", "backbone", "mvc/models/BulletinModel",
		"mvc/collections/BulletinsCollection",
		"mvc/collections/CategoriesCollection", "mvc/views/BulletinView",
		"mvc/views/DetailView", "mvc/views/CategoryView" ], function(token, $,
		Backbone, BulletinModel, BulletinsCollection, CategoriesCollection,
		BulletinView, DetailView, CategoryView) {
	var CategoryRouter = Backbone.Router.extend({
		initialize : function() {
			var collection = new BulletinsCollection();
			this.bulletinView = new BulletinView({
				el : "#myPage",
				collection : collection
			});
			this.detailView = new DetailView({
				el : "#detailPage",
				collection : collection
			});
			this.categoryView = new CategoryView({
				el : "#J_bulletin_category",
				collection : new CategoriesCollection()
			});
			var self = this;
			$(document).on('tap', '.back', function(e) {
				self.back = true;
				window.back = true;
				window.history.back();
				return false;
			});
			$(document).on('tap', '.refresh', function(e) {
				alert("refresh");
				window.location = window.location.href;
				return false;
			});
			Backbone.history.start();
		},
		routes : {
			"" : "list",
			"detail?:id" : "detail",
			"list?:type" : "list",
			"list?:type/:page" : "list",
			"categoryOpen" : "categoryOpen",
			"categoryClose" : "categoryClose"
		},
		// Home method
		list : function(type, pageNo) {
			$.mobile.changePage("#myPage", {
				changeHash : false,
				reverse : this.back
			});
			if (this.back) {
				this.back = false;
				return;
			}
			this.back = false;
			type = type || "all";
			$.mobile.loading("show");
			var categoryModel = this.categoryView.collection.get(type);
			this.bulletinView.collection.fetch({
				"type" : type,
				"pageNo" : pageNo,
				"category" : categoryModel
			});
			$("#mypanel").panel("close");
		},
		detail : function(id) {
			window.lastScroll = $(window).scrollTop();
			window.back = false;
			this.detailView.collection.detail(id);
			// 解决切换页面闪的问题
			$.mobile.changePage("#detailPage");
			return false;
		},
		categoryOpen : function() {
			// 打开panel
			$("#mypanel").panel("open");
			// 加载panel数据
			this.categoryView.collection.fetch();
			return false;
		},
		categoryClose : function() {
			$("#mypanel").panel("close");
			// 首页加载
			this.back = true;
			this.list();
			return false;
		}
	});
	return CategoryRouter;
});