
define('mvc/collections/CategoriesCollection',[ "jquery", "backbone", "mvc/models/CategoryModel" ], function($,
		Backbone, CategoryModel) {
	var Collection = Backbone.Collection.extend({
		initialize : function(models, options) {
		},
		model : CategoryModel,
		sync : function(method, model, options) {
			var self = this;
			var xhr = Backbone.ajax({
				url : window.local?"category.json":"/ecp/ecppub/getCodeName.json",
				type : 'GET',
				dataType:"json",
				data : {
					"key" : "CODENAME_BULLETIN_TYPE",
					"param" : ""
				},
				success : function(data) {
					data.items.splice(0,0,{code:"all",name:"全部公告"});
					self.trigger("added",data.items);
					self.reset(data.items);
				}
			});
			return xhr;
		}
	});
	return Collection;
});